import React, { useState, useEffect, useRef } from 'react';
import logo from './logo.png'; // Replace with your logo path
import { Link } from 'react-scroll';
import { theme } from '../../theme.js'

const Navbar = () => {
  const [isMobile, setIsMobile] = useState(false);
  const [showMenu, setShowMenu] = useState(false);
  const menuRef = useRef(null);

  const toggleMenu = () => {
    setShowMenu(!showMenu);
  };

  const handleResize = () => {
    setIsMobile(window.innerWidth <= 768);
    if (window.innerWidth > 768) {
      setShowMenu(false); // Ensure menu is hidden on larger screens
    }
  };

  const handleClickOutside = (event) => {
    if (menuRef.current && !menuRef.current.contains(event.target)) {
      setShowMenu(false);
    }
  };

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 768);
    handleResize(); // Set the initial value
    window.addEventListener('resize', handleResize);
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      window.removeEventListener('resize', handleResize);
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const containerStyle = {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: '10px 20px',
    backgroundColor: '#ffff',
  };

  const logoStyle = {
    fontSize: '1.5rem',
    fontWeight: 'bold',
    color: '#333',
    textDecoration: 'none',
  };

  const centerStyle = {
    position: 'absolute',
    left: '50%',
    transform: 'translateX(-50%)',
    display: 'flex',
    alignItems: 'center',
  };

  const hamburgerStyle = {
    display: isMobile ? 'block' : 'none',
    cursor: 'pointer',
    margin: 0,
  };

  const menuIconStyle = {
    width: '25px',
    height: '3px',
    backgroundColor: '#333',
    margin: '4px 0',
    transition: '0.4s',
  };

  const navLinksStyle = {
    listStyleType: 'none',
    display: isMobile ? 'none' : 'flex',
    flexDirection: 'row',
    gap: '20px',
  };

  const navLinkItemStyle = {
    textAlign: 'center',
  };

  const navLinkStyle = {
    textDecoration: 'none',
    fontSize: '1rem',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    cursor: 'pointer'
  };

  const rightStyle = {
    display: 'flex',
    alignItems: 'center',
  };

  const getQuoteBtnStyle = {
    padding: '10px 20px',
    backgroundColor: theme.colors.primary,
    color: '#fff',
    textDecoration: 'none',
    borderRadius: '5px',
    marginRight: '20px',
    fontFamily: theme.fonts.primary,
    cursor: 'pointer',
  };

  const drawerStyle = {
    position: 'fixed',
    top: 0,
    right: showMenu ? 0 : '-100%',
    width: '250px',
    height: '100%',
    backgroundColor: '#f0f0f0',
    boxShadow: '-2px 0 5px rgba(0,0,0,0.5)',
    transition: 'right 0.3s ease-in-out',
    display: isMobile ? 'block' : 'none',
  };

  const drawerNavLinksStyle = {
    listStyleType: 'none',
    padding: '20px',
    display: 'flex',
    flexDirection: 'column',
    gap: '20px',
  };

  return (
    <nav style={containerStyle}>
      <div style={{ flex: 1 }}>
        <a href="/" style={logoStyle}>
          <img src={logo} alt="Logo" style={{ width: '50px', height: 'auto' }} />
        </a>
      </div>
      <div style={centerStyle}>
        <ul style={navLinksStyle}>
          <li style={navLinkItemStyle}><Link to="services" smooth={true} duration={500} style={navLinkStyle}>Services</Link></li>
          <li style={navLinkItemStyle}><Link to="about-us" smooth={true} duration={500} style={navLinkStyle}>About Us</Link></li>
          <li style={navLinkItemStyle}><Link to="contact-us" smooth={true} duration={500} style={navLinkStyle}>Contact Us</Link></li>
          <li style={navLinkItemStyle}><a href="https://bhavukjain.com" target="_blank" rel="noopener noreferrer" style={navLinkStyle}>Blog</a></li>
        </ul>
      </div>
      <div style={rightStyle}>
        <Link to="contact-us" smooth={true} duration={500} style={getQuoteBtnStyle}>Get Quote</Link>
        <div style={hamburgerStyle} onClick={toggleMenu}>
          <div style={menuIconStyle}></div>
          <div style={menuIconStyle}></div>
          <div style={menuIconStyle}></div>
        </div>
      </div>
      <div style={drawerStyle} ref={menuRef}>
        <ul style={drawerNavLinksStyle}>
          <li style={navLinkItemStyle}><Link to="services" smooth={true} duration={500} style={navLinkStyle} onClick={toggleMenu}>Services</Link></li>
          <li style={navLinkItemStyle}><Link to="about-us" smooth={true} duration={500} style={navLinkStyle} onClick={toggleMenu}>About Us</Link></li>
          <li style={navLinkItemStyle}><Link to="contact-us" smooth={true} duration={500} style={navLinkStyle} onClick={toggleMenu}>Contact Us</Link></li>
          <li style={navLinkItemStyle}><a href="https://bhavukjain.com" target="_blank" rel="noopener noreferrer" style={navLinkStyle} onClick={toggleMenu}>Blog</a></li>
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
