import React, { useState, useEffect } from 'react';
import { theme } from '../../theme.js'

const Services = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const containerStyle = {
    textAlign: 'center',
    margin: '0 auto',
    padding: '20px',
  };

  const headerStyle = {
    marginBottom: '20px',
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const optionsStyle = {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    alignItems: 'stretch',
  };

  const optionStyle = {
    width: '30%', // 30% width for each option on larger screens
    margin: '10px',
    padding: '20px',
    border: '1px solid #ccc',
    borderRadius: '8px',
    textAlign: 'center',
    boxShadow: '0px 2px 4px rgba(0, 0, 0, 0.1)', // Box shadow for a card-like appearance
    boxSizing: 'border-box', // Ensures padding and border are included in the width
    display: 'flex',
    flexDirection: 'column',
    ...(isMobile && {
      width: '100%', // Full width on mobile
      margin: '10px auto', // Centered on mobile
    }),
    backgroundColor: theme.colors.secondary
  };

  const optionImageStyle = {
    display: 'block',
    margin: '0 auto 10px',
    width: '50px',
  };

  const optionTextStyle = {
    marginTop: 0,
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    fontSize: '1.2rem',
  };

  const optionSubTextStyle = {
    marginTop: 0,
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    fontSize: '1rem',
  };

  return (
    <div style={containerStyle}>
      <h1 style={headerStyle}>{theme.services.title}</h1>
      <div style={optionsStyle}>
        {theme.services.services.map((service, index) => (
          <div key={index} style={optionStyle}>
            <div style={optionImageStyle}>{service.icon}</div>
            <p style={optionTextStyle}>{service.title}</p>
            <p style={optionSubTextStyle}>{service.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Services;
