import React, { useState, useEffect } from 'react';
import { theme } from '../../theme.js'
import { GrLinkedin } from "@react-icons/all-files/gr/GrLinkedin";
import { GrTwitter } from "@react-icons/all-files/gr/GrTwitter";
import { MdArrowOutward } from "react-icons/md";

const AboutUs = () => {
  const [selectedFounder, setSelectedFounder] = useState(null);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const openPopup = (founder) => {
    setSelectedFounder(founder);
  };

  const closePopup = () => {
    setSelectedFounder(null);
  };

  const containerStyle = {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '20px',
    textAlign: 'center',
  };

  const titleStyle = {
    fontSize: '2rem',
    fontWeight: 'bold',
    marginBottom: '40px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const foundersContainerStyle = {
    display: 'flex',
    justifyContent: 'center',
    gap: '80px',
    marginBottom: '20px',
    textAlign: 'left',
    flexWrap: isMobile ? 'wrap' : 'nowrap'
  };

  const founderStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    cursor: 'pointer'
  };

  const imageStyle = {
    width: '150px',
    height: '150px',
    borderRadius: '50%',
    marginBottom: '10px',
    backgroundColor:theme.colors.secondary,
    boxShadow: '0px 2px 4px rgba(0, 0, 0, 0.1)',
    border: '1px solid #ccc',
  };

  const nameAndTitleContainer = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
    overflow: 'hidden',
  };

  const nameStyle = {
    fontSize: '1.2rem',
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    display: 'flex',
    alignItems: 'center',
  };

  const titleTextStyle = {
    fontSize: '1rem',
    fontFamily: theme.fonts.primary,
    color: theme.colors.primary,
    marginLeft: '8px',
  };

  const popupOverlayStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 1000,
  };

  const popupContentStyle = {
    backgroundColor: '#fff',
    padding: '20px',
    borderRadius: '8px',
    maxWidth: '500px',
    width: '80%',
    maxHeight: '80%', // Ensure popup content does not exceed 80% of the viewport height
    overflowY: 'auto', // Enable vertical scrolling if content overflows
    textAlign: 'center',
  };

  const popupImageStyle = {
    width: '150px',
    height: '150px',
    borderRadius: '50%',
    marginBottom: '10px'
  };

  const popupNameContainer = {
    display: 'flex',
    flexDirection: 'row', 
    alignItems: 'center', 
    justifyContent:'center', 
    // marginLeft:'80px'
  }

  const popupNameStyle = {
    fontSize: '1.2rem',
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const popupTitleTextStyle = {
    fontSize: '1rem',
    marginBottom: '10px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const popupDescriptionStyle = {
    fontSize: '0.8rem',
    color: '#555',
    fontFamily: theme.fonts.primary,
  };

  const popupIconsStyle = {
  display: 'flex',
  justifyContent: 'flex-end',
  alignItems: 'center',
  marginTop: '10px',
};

  const closeButtonStyle = {
    marginTop: '20px',
    padding: '10px 20px',
    backgroundColor: '#004ca3',
    color: '#fff',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer'
  };

  const rightSectionStyle = {
    alignItems: 'center',
    gap: '10px',
    marginLeft:'10px'
  };


  return (
    <div style={containerStyle}>
      <h2 style={titleStyle}>{theme.aboutus.title}</h2>
      <div style={foundersContainerStyle}>
        {theme.aboutus.founders.map((founder) => (
          <div key={founder.id} style={founderStyle} onClick={() => openPopup(founder)}>
            <img src={founder.image} alt={founder.name} style={imageStyle} />
            <div style={{...popupNameContainer, marginLeft:'10px'}}>
              <div style={nameStyle}>{founder.name}</div>
              <MdArrowOutward size={25} style={{marginLeft:8, marginBottom: 8}}/>
            </div>
            <div style={titleTextStyle}>{founder.title}</div>
          </div>
        ))}
      </div>

      {selectedFounder && (
        <div style={popupOverlayStyle} onClick={closePopup}>
          <div style={popupContentStyle} onClick={(e) => e.stopPropagation()}>
            <img src={selectedFounder.image} alt={selectedFounder.name} style={popupImageStyle} />
            <div style={popupNameContainer}>
              <div style={popupNameStyle}>{selectedFounder.name}</div>
                <a href={selectedFounder.linkedin} target="_blank" rel="noopener noreferrer">
                  <GrLinkedin size={25} style={{margin:8, color:theme.colors.primary}}/>
                </a>
                {selectedFounder.twitter &&
                  <a href={selectedFounder.twitter} target="_blank" rel="noopener noreferrer">
                    <GrTwitter size={25} style={{margin:8, color:theme.colors.primary}}/>
                  </a>
                }
            </div>
            <div style={popupTitleTextStyle}>{selectedFounder.title}</div>
            {selectedFounder.description.map((paragraph, index) => (
              <p style={popupDescriptionStyle} key={index}>{paragraph}</p>
            ))}
            <button style={closeButtonStyle} onClick={closePopup}>{theme.aboutus.closeButton}</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default AboutUs;
